<template>
  <transition name="fade_in" mode="out-in">
    <div class="helloreading">
      <transition name="fade_top">
        <div class="bookhead" v-show="edgshow">
          <span class="el-icon-arrow-left" style="font-size:24px;" @click="quit"></span>
          <span class="bookname">{{bookbase.bookname}}</span>
          <span class="author">{{bookbase.author}}</span>
          <span class="sectionlist" @click="toogledraw">目录</span>
        </div>
      </transition>
      <div
        class="content"
        @click.prevent.stop="toggleedg()"
        infinite-scroll-distance="-100px"
        v-infinite-scroll="loadsection"
        :infinite-scroll-disabled="disabled"
        infinite-scroll-immediate="false"
        v-loading="loadflag"
      >
        <div
          class="chapter"
          :class="'chapter'+chapter.section"
          v-for="(chapter,index) in bookdetail"
          :key="index"
        >
          <h4 class="title">{{chapter.section_title}}</h4>
          <div class="text">{{chapter.content}}</div>
        </div>
        <p v-if="booksection!==currentsection" class="scroll-tip">
          <span class="el-icon-loading"></span> 加载中...
        </p>
        <p v-if="booksection===currentsection" class="scroll-tip">没有更多了</p>
      </div>
      <transition name="fade_bottom">
        <div class="bookfoot" v-show="edgshow">
          <div class="item" @click.prevent.stop="fontsetting">
            <span class="el-icon-brush"></span>
            <span class="text">字体</span>
          </div>
          <div class="item" @click.prevent.stop="bgsetting">
            <span class="el-icon-setting"></span>
            <span class="text">背景</span>
          </div>
        </div>
      </transition>
      <el-drawer title="目录" :visible.sync="drawer" :show-close="false" size="40%">
        <ul>
          <li class="section-item">(部分功能未开放)</li>
          <li
            class="section-item"
            v-for="(section,index) in sections"
            :key="index"
            @click="jumpto(section.section)"
          >
            <span v-if="index!==0">{{section.section+'.'}}</span>
            {{section.section_title}}
          </li>
        </ul>
      </el-drawer>
    </div>
  </transition>
</template>

<script>
import { getbookdetail, getsections } from "@/api/index.js";
export default {
  name: "helloreading",
  data() {
    return {
      bookdetail: [],
      booksection: -1,
      currentsection: 0,
      bookbase: {},
      sections: [],
      edgshow: true,
      drawer: false,
      disabled: false,
      loadflag: false
    };
  },
  created() {
    this.getcontent(this.$route.params.bookId);
    this.getbooksections(this.$route.params);
  },
  // beforeRouteEnter(){

  // },
  methods: {
    getcontent(bookId, booksection = 0) {
      this.loadflag = true;
      let params = { bookId, booksection };
      getbookdetail(params).then(res => {
        this.loadflag = false;
        if (res.data.code === 201) {
          this.bookbase = res.data.data.bookbase;
          this.bookdetail = res.data.data.sections;
        } else if (res.data.code === 200) {
          this.currentsection = this.currentsection + 1;
          this.bookdetail = this.bookdetail.concat(res.data.data.sections[0]);
        }
      });
    },
    getbooksections(bookId) {
      getsections(bookId).then(res => {
        if (res.data.code === 200) {
          this.sections = res.data.data;
        }
      });
    },
    loadsection() {
      if (this.currentsection === this.booksection) {
        this.disabled = true;
      } else {
        this.getcontent(this.$route.params.bookId, this.currentsection + 1);
      }
    },
    jumpto(sectionid) {
      this.$notify({
        title: "未上线",
        message: "此功能正在研发中...",
        iconClass: "el-icon-info",
        duration: 2500
      });
    },
    toggleedg() {
      this.edgshow = !this.edgshow;
    },
    toogledraw() {
      this.drawer = !this.drawer;
    },
    quit() {
      this.$router.go(-1);
    },
    reset() {
      this.bookdetail = [];
      this.booksection = -1;
      this.currentsection = 0;
      this.bookbase = {};
      this.sections = [];
      this.edgshow = true;
      this.drawer = false;
    }
  },
  watch: {
    sections(booksection) {
      this.booksection = this.sections[this.sections.length - 1]["section"];
    }
  },
  beforeDestory() {
    this.reset();
  }
};
</script>

<style scoped lang="scss">
@import "@/assets/scss/index.scss";
.bookbase {
  width: 100%;
  position: fixed;
  height: 60px;
  line-height: 60px;
  @include color($background-color-theme);
  @include background($font-color-theme, 0.8);
  z-index: 100;
}

.helloreading {
  @include font1_18;
  & .bookhead {
    @extend .bookbase;
    left: 0;
    top: 0;
    & .bookname {
      @include font1_24;
      font-weight: 50;
      vertical-align: middle;
      margin: 0 20px 0 10px;
    }
    & .author {
      @include fontxing_14;
      letter-spacing: 1px;
      font-weight: 1000;
      vertical-align: bottom;
    }
    & .sectionlist {
      @include font1_24;
      cursor: pointer;
      float: right;
      padding-right: 10px;
    }
  }
  & .content {
    @include divfix;
    @include fontnor_14;
    line-height: 1.5;
    font-weight: 1000;
    overflow-y: scroll;
    & .chapter {
      margin-bottom: 30px;
      & .title {
        @include font1_24;
        padding: 10px;
        text-align: center;
        margin-bottom: 10px;
      }
    }
    & .text {
      white-space: pre-wrap;
    }
    & .scroll-tip {
      height: 40px;
      line-height: 40px;
      text-align: center;
    }
  }
  & .bookfoot {
    @extend .bookbase;
    left: 0;
    bottom: 0;
    display: flex;
    & .item {
      flex: 1;
      text-align: center;
      &:hover {
        cursor: pointer;
      }
      & span {
        @include fontnor_14;
        vertical-align: middle;
      }
      & .text {
        @include font1_20;
      }
    }
  }
}
.el-drawer {
  @include font1_26;
}
.el-dialog__wrapper /deep/ .el-drawer__body {
  overflow-y: scroll;
}
.el-drawer .section-item {
  width: 100%;
  cursor: pointer;
  @include font1_20;
  letter-spacing: 1px;
  padding: 10px;
  border-bottom: 1px #ccc dashed;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.fade_top-enter-active,
.fade_top-leave-active {
  transition: all 0.5s;
}
.fade_top-enter,
.fade_top-leave-to {
  transform: translateY(-100%);
  opacity: 0;
}
.fade_bottom-enter-active,
.fade_bottom-leave-active {
  transition: all 0.25s;
}
.fade_bottom-enter,
.fade_bottom-leave-to {
  transform: translateY(60px);
  opacity: 0;
}
.fade_in-enter-active,
.fade_in-leave-active {
  transition: all 0.25s;
}
.fade_in-enter,
.fade_in-leave-to {
  transform: scale(1.2);
  opacity: 0;
}
</style>